body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  text-align: center;
  background-color: rgba(100, 59, 26, 0.479);
}
.navbar-wrapper {
  background-color: rgba(204, 204, 204, 0.3);
  display: flex;
  line-height: 1em;
  justify-content: space-around;
  font-weight: 600;
  margin: 0 auto; }

.navbar-brand {
  margin-top: 1em;
  cursor: pointer; }

.navbar-brand a {
  text-decoration: none;
  color: #000; }

.menu-link {
  display: inline-flex;
  list-style: none;
  padding: 0 1em; }

.menu-link a {
  text-decoration: none;
  color: #000; }

.link {
  text-decoration: none;
  color: #000; }

.active {
  border-bottom: 2px solid green; }

.app-header {
  margin-top: 0;
  background-color: #36bdcf;
  padding: 0.6em 0;
  color: #fff;
  font-weight: 400;
  font-size: 1.6em;
  max-width: 1280px;
  margin: 0 auto;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  box-shadow: 0px 10px 5px -7px rgba(0, 0, 0, 0.15); }

.todo-app-wrapper {
  max-width: 900px;
  margin: 0 auto; }

.tasks-wrapper {
  margin-top: 5em; }

.single-task {
  margin: 1em auto;
  padding: 0.8em;
  border: 2px solid #ccc;
  width: 60%;
  border-radius: 10px; }
  .single-task:hover {
    cursor: pointer;
    background-color: rgba(6, 134, 134, 0.473); }

.task-content {
  font-size: 1.2em;
  font-weight: 600; }

.tasks-finish {
  font-size: 1.6em;
  color: #24e20b;
  background-color: #777575;
  width: 70%;
  margin: 0 auto;
  padding: 0.5em 0;
  border-radius: 15px;
  margin-bottom: 2em; }

.new-task-form {
  background-color: #666;
  padding: 1em;
  width: 50%;
  margin: 0 auto;
  border-radius: 12px; }

.task-form-label {
  display: block;
  font-size: 1.3em;
  font-weight: 400;
  margin-bottom: 1em; }

.task-form-input {
  width: 90%;
  margin: 0 auto;
  border-radius: 7px;
  border: 0;
  padding: 0.4em;
  font-size: 1.3em; }

.task-form-input:focus {
  border: 3px solid #14fc14;
  outline: 0; }

